<template>
  <div>
    <nav-bar :name="title"></nav-bar>
      <v-container class="text-center">
        <img :src="img" alt="" width="100px">
        <h5> {{ data_user.username }} </h5>
        <h6> {{ data_store.name }} </h6>
      </v-container>

    <v-card class="mx-auto my-auto">
      <v-container>
        <v-tabs
          v-model="tab"
          background-color="primary"
          dark
        >
          <v-tab>
            Personal info
          </v-tab>
          <v-tab>
            Stores Owned
          </v-tab>
          <v-tab>
            Referrals
          </v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
          <v-tab-item>
            <v-card flat>
              <v-row>
                <v-col cols="10" class="text-left">
                  <v-card-subtitle> About Me </v-card-subtitle>
                </v-col>
        <v-spacer></v-spacer>
        <v-col cols="2" class="text-right">
          <v-btn icon>
            <router-link :to="{ name: 'UpdateUser', params: { 'id': data_user.unique_id }}">
              <v-icon right> mdi-pencil </v-icon>
            </router-link>
          </v-btn>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
        <h6 class="text-left"> Full name </h6>
          <v-text-field
          outlined
          dense
          disabled
          :value="fullname"
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" sm="6" v-for="(item, id) in options" :key="id">
        <h6 class="text-left"> {{ item.text }} </h6>
        <v-text-field
        outlined
        dense
        disabled
        :value="item.data"
        ></v-text-field>
        </v-col>
      </v-row>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      
      </v-container>
    </v-card>

<footer-nav></footer-nav>

  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import FooterNav from '@/components/Footer';
  import UserService from '@/services/UserService';

  export default {
    components: {
      NavBar,
      FooterNav
    },
 
    data: () => ({
    title: 'PROFILE',
    tab: null,
    fullname: '',
    img: '',
    options: [
      {
        text: "Phone number",
        data: ""
      },
      {
        text: "Email",
        data: ""
      },
      {
        text: "Admin",
        data: ""
      },
      {
        text: "Unique ID",
        data: ""
      },
      {
        text: "Username",
        data: ""
      },
      {
        text: "Joined on",
        data: ""
      },
    ]
  }),

  
  mounted() {
    const months = ["JAN", "FEB", "MAR","APR", "MAY", "JUN", "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"];

    UserService
      .get_profile()
      .then(res => {
        let current_datetime = new Date(res.date_joined)
        let formatted_date = current_datetime.getDate() + "-" + months[current_datetime.getMonth()] + "-" + current_datetime.getFullYear()
        
        this.fullname = res.first_name + ' ' + res.last_name;
        this.options[0].data = res.phone_number;
        this.options[1].data = res.email;
        this.options[2].data = res.is_staff ? 'Yes' : 'No';
        this.options[3].data = res.unique_id;
        this.options[4].data = res.username;
        this.options[5].data = formatted_date;
        this.img = 'https://trodapp.com' + res.img;
      })

  },

  computed: {
    data_store(){
        return this.$store.state.auth.store_user;
    },
    
    data_user(){
        return this.$store.state.auth.user;
    },
  }

}
</script>