<template>
  <v-footer>
    <v-card
      flat
      tile
      width="100%"
      class="text-center"
    >
      <v-card-text v-if="isStaff">
        <router-link v-for="(item, id) in modules" :key="id" :to="item.to">
        <v-btn
          class="mx-4"
          icon
        >
          <v-icon size="24px">
            {{ item.icon }}
          </v-icon>
        </v-btn>
        </router-link>
      </v-card-text>

      <v-card-text v-else>
        <router-link v-for="(item, id) in items" :key="id" :to="item.to">
        <v-btn
          class="mx-4"
          icon
        >
          <v-icon size="24px">
            {{ item.icon }}
          </v-icon>
        </v-btn>
        </router-link>
      </v-card-text>
    </v-card>
  </v-footer>
</template>

<script>
  export default {
    name: 'FooterNav',

    data: () => ({
      modules: [
        {
          icon: "mdi-currency-usd",
          to: "/sales"
        },
        {
          icon: "mdi-shopping",
          to: "/stores"
        },
        {
          icon: "mdi-account-supervisor",
          to: "/users"
        },
        {
          icon: "mdi-credit-card-outline",
          to: "/payments"
        },
        {
          icon: "mdi-account-group",
          to: "/referrals"
        },
        {
          icon: "mdi-file-document-outline",
          to: "/invoices"
        },
      ],
      items: [
        {
          icon: "mdi-currency-usd",
          to: "/sales"
        },
        {
          icon: "mdi-shopping",
          to: "/viewstore"
        },
        {
          icon: "mdi-credit-card-outline",
          to: "/payments"
        },
        {
          icon: "mdi-account-group",
          to: "/referrals"
        },
        {
          icon: "mdi-file-document-outline",
          to: "/invoices"
        },
      ],
    }),

    computed: {
      isStaff(){
        if (this.$store.state.auth.user.is_staff)
          return true;

        return false;
      },
    },
  }
</script>